      baipishuIcon,
      guanweiIcon,
      shangwuIcon,
      stopIcon,
      zanIcon,
      zanAIcon,
      caiIcon,
      caiAIcon,
      toAskIcon,
      activeBaipishuIcon,
      recordindIcon,
      volumeImg,
      mutedImg,
      baipishuPng,
      guanweiPng,
      shangwuPng,
      isRecordPng,
      recordingPng,
      think1Png,
      thinkPng,
      logohiPng,
      logohijPng
    });

    const state = reactive({
      questions: [
        { label: "公司有哪些产品和服务？" },
