        margin-right: 18px;
      }
      .icon {
        cursor: pointer;
        width: 24px;
      }
      .close-icon {
        width: 23px;
        margin-left: 16px;
      }

      .blockBtwn {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        margin-right: 16px;
