        <div class="tool-bar" v-if="!isSmall"> 
          <span class="blockBtwn" :class="{blockBtwn:((formShow?linkType!==-1||step!==-1:ingIndex<4))||isPCFullSreen||oneSreen}">
            <van-image
              :src="preImg"
              class="pre-icon icon"
              :style="(ingIndex !== 0 || linkBar || formShow)?'':'opacity: 0;cursor: auto;'"
