            class="close-icon icon"
          ></van-image>
        </div>
        

        
      <!-- 菜单 -->
      <div class="p-box trans-opactity" :class="{ isSmall, 
        hide: isSmall|| formShow&&!isFrame&&oneSreen|| isPCFullSreen&&formShow&&!isFrame ,
        nobarFirstCome,
        'barFirstCome':!nobarFirstCome&&!isSmall}">
        <template v-if="!linkBar">
          <p
            class="ibar "
            :class="{ ing: ingIndex === 1 ,barHide}"
            @click="toIngIndex(1, 0)"
          >
            <div>
              <span>A</span>
              <span>了解企业概况</span>
            </div>
             <div>
              <span>A</span>
              <span>了解企业概况</span>
            </div>
          </p>
          <p
            class="ibar"
            :class="{ ing: ingIndex === 2 ,barHide}"
            @click="toIngIndex(2, 5)"
          >
            <div>
              <span>B</span>
              <span>了解主营业务</span>
            </div>
                <div>
              <span>B</span>
              <span>了解主营业务</span>
            </div>
          </p>
          <p
            class="ibar"
            :class="{ ing: ingIndex === 3 ,barHide}"
            @click="toIngIndex(3, 10)"
          >
            <div>
              <span>C</span>
              <span>商务联系</span>
            </div>
              <div>
              <span>C</span>
              <span>商务联系</span>
            </div>
          </p>
        </template>
        <template v-else>
          <p
            class="ibar"
