            </div>
            <div>
              <span>A</span>
              <span>微信关注，获悉最新动态</span>
            </div>
          </p>
          <p
            class="ibar"
            :class="{ ing: linkType === 1,barHide:barHide&&!oneSreen }"
            @click="toLinkIndex(1,oneSreen)"
          > 
            <div>
              <span>B</span>
              <span>商务联系，与业务专家对话</span>
            </div>
            <div>
             <span>B</span>
              <span>商务联系，与业务专家对话</span>
            </div>
          </p>
        </template>
       
      </div>

       <!-- 智能问答入口 -->
         <robotIn :oneSreen="oneSreen" @robotClick="robotClick" v-if="!isFrameOneSreen&&!isSmall"/>
      </div>


      <div class="leaveForm" v-if="askLeave">
        <p class="tit">要放弃填表吗？</p>
        <p class="btn-bar">
          <div class="light i-btn"  @click="closeForm()">离开</div>
          <div class="i-btn" @click="askLeave = false">留下</div>
        </p>
      </div>

      <!-- 表单 -->
      <div class="formBox slide-midleft" :class="{ hide: !formShow || isSmall||isFrameOneSreen,contOnlyForm:(from&&from.includes('fromRobot')&&formShow)}">
        <div class="tool-bar" v-if="oneSreen||(from&&from.includes('fromRobot')&&formShow)">
          <van-image
            :src="preImg"
            class="pre-icon icon"
            @click.stop="closeMForm()"
          ></van-image>
          <!-- linkType === 1&&oneSreen?askLeave=true: -->
        </div>
        <div v-if="linkType === 0" class="wechatLink slide-midleft">
 
          <van-image :src="qrImg"></van-image>
          <p>微信扫码，关注我们</p>
        </div>

        <formTable
          v-if="linkType === 1"
          :step="step"
          :oneSreen="oneSreen"
          :from="from"
          @setStep="setStep"
          @closeForm="closeForm"
        ></formTable>
      </div>

      <!-- 智能问答 -->
       <robot class="slide-midleft" v-show="isFrameOneSreen" :visiable="isFrameOneSreen" :BpsVisible="BpsVisible" :oneSreen="oneSreen" @toPreVideo="toPreVideo" @toLinkIndex="toLinkIndex" @closeForm="closeForm"/>
    </div>

  </div>
</template>

<script>
import { defineComponent, onMounted, reactive, toRefs, ref,watch,onUnmounted,onBeforeUnmount } from "vue";
import { useRouter, useRoute } from "vue-router";
import "vue3-video-play/dist/style.css";
import bus from "vue3-eventbus";
import store from "@/store";

import _ from "lodash";
import maxImg from "@/assets/img/ai/max.png";
import closeImg from "@/assets/img/ai/close.png";
import volumeImg from "@/assets/img/ai/volume.png";
import mutedImg from "@/assets/img/ai/muted.png";
import preImg from "@/assets/img/ai/pre.png";
import posterImg from "@/assets/img/ai/poster.jpg";
import { logs,calcTimes,generateRandomString } from "@/utils/index";
import formTable from "./formTable.vue";
import robot from "./robot.vue";
import robotIn from "@/components/aiPub/robotIn.vue";

export default defineComponent({
  components: {
    formTable,
    robot,
    robotIn
  },
  setup(props) {
    const router = useRouter();
    const route = useRoute();
    const homeVideo = ref(null);

    onMounted(() => {
      // homeVideo.value.muted = true;
      let videoPlay00 = document.getElementById("videoPlay-1");
      let videoPlay0 = document.getElementById("videoPlay0");
      let videoPlay1 = document.getElementById("videoPlay1");
      let videoPlay2 = document.getElementById("videoPlay2");

      // console.log(window.top);
      // 监听和播放控制
      // videoPlay.addEventListener(
      //   "timeupdate",
      //   function () {
      //     var timeDisplay;
      //     //用秒数来显示当前播放进度
      //     timeDisplay = Math.floor(videoPlay.currentTime);
      //     // console.log(timeDisplay);
      //     //当视频播放到 4s的时候做处理
      //     if (timeDisplay == 5) {
      //       //处理代码
      //       state.ingIndex = 1;
      //     }
      //     if (timeDisplay == 10) {
      //       //处理代码
      //       state.ingIndex = 2;
      //     }
      //   },
      //   false
      // );
      videoPlay0.addEventListener(
        "play",
        function () {
          state.isPlayed = false
        },
        false
      );
      
        videoPlay0.addEventListener('ended', function () { 
          console.log('state.isSmall: ', state.isSmall);
          if(!state.isSmall) toIngIndex(1)
        }, false);
          videoPlay1.addEventListener('ended', function () { 
          if(!state.isSmall) toIngIndex(2)
        }, false);
        videoPlay2.addEventListener('ended', function () { 
          if(!state.isSmall) toIngIndex(3)
        }, false);

        // isInmobile       
         const userAgent = navigator.userAgent;
          const mobileKeywords = ['Android', 'iPhone', 'iPad', 'iPod', 'Windows Phone'];
          var is_mobile_agent = mobileKeywords.some(keyword => userAgent.includes(keyword));
         state.isInmobile = (route.query.isFrame||window.location.ancestorOrigins.length)&&is_mobile_agent
       
        sessionStorage.setItem('sessionId',generateRandomString()) ;
        if(state.oneSreen&&!state.inPageTime) state.inPageTime = Date.now() //记录初入页面时间
      
         
    });

    onBeforeUnmount(()=>{
       if(state.oneSreen&&state.inPageTime) {
         logs(8,998,calcTimes(state.inPageTime)) // location.protocol=="https:"&& logs(8,998,calcTimes(state.inPageTime)) 
          console.log('移动端 停留时长calcTimes: ', 8,998,calcTimes(state.inPageTime));
        }
    })

    
     const miniVideoLoad = () => {
       if(state.isSmall&&!state.ifShow) {
        state.ifShow = true
        console.log('videoPlay: 初始化',state.isSmall,state.ifShow);
         window.top.postMessage({
          ifShow: true
         }, window.location.ancestorOrigins[0]);


       }

     }
    const state = reactive({
      ifShow: false,
      // 1024px
      oneSreen: !route.query.isFrame&&!window.location.ancestorOrigins.length,// && window.outerWidth < 1024 ,//单屏显示
      isFrameOneSreen: false, //pc端查看智能问答
      isFrame: route.query.isFrame||window.location.ancestorOrigins.length||false, //作为插件使用在其他网站，显示在右下角
      isSmall: route.query.isFrame||window.location.ancestorOrigins.length||false, //作为插件使用时的初始状态是小块屏幕，可以点击全屏按钮放大，后面可至双屏
      isPCFullSreen: (!route.query.isFrame)&&(!window.location.ancestorOrigins.length)&&window.outerWidth > 1024, //是全屏的，且屏幕宽大于1024px
      isInmobile: false, //是否内嵌在iframe的移动端

      muted: true, // 默认情况下将会消除任何音频。
      ingIndex: (!route.query.isFrame&&!window.location.ancestorOrigins.length)?0:-1,
      formShow: false,
      maxImg, 
      miniMP4: 'https://image.ymtech.info/aistatic/status_20240619.mp4',
      oneMP4: (location.protocol=="https:"?'https://aigc-cdn.kreadoai.com':'https://aigc-test-cdn.kreadoai.com')+'/ai_guide/eclicktech/resource/welcome.mp4',//'https://image.ymtech.info/aistatic/welcome_20240619.mp4',
      twoMP4: 'https://image.ymtech.info/aistatic/aboutdianyi_20240619.mp4',
      threeMP4: (location.protocol=="https:"?'https://aigc-cdn.kreadoai.com':'https://aigc-test-cdn.kreadoai.com')+'/ai_guide/eclicktech/resource/mainbusiness.mp4',//'https://image.ymtech.info/aistatic/mainbusiness_20240619.mp4',
      fourMP4: (location.protocol=="https:"?'https://aigc-cdn.kreadoai.com':'https://aigc-test-cdn.kreadoai.com')+'/ai_guide/eclicktech/resource/contact.mp4',//'https://image.ymtech.info/aistatic/contact_20240619.mp4',
      closeImg,
      volumeImg,
      mutedImg,
      preImg,
      qrImg: "https://image.ymtech.info/aistatic/20240603-180512.png",
      posterImg,
      ingList: [{}, {}, {}],
      linkBar: false,
      linkType: -1, //0: 微信扫码， 1：表单填写
      step: 4, //form填写的第几步，共四步
      askLeave: false,
      isMobile: window.innerWidth < 500,
      homeVideoPlay: homeVideo,
      options: {
        // /playbackRates: .btnNextBorder[0.5, 1.0, 1.5, 2.0], // 可选的播放速度
        autoplay: true, // 如果为true,浏览器准备好时开始回放。

        loop: true, // 是否视频一结束就重新开始。
        preload: "auto", // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
        language: "zh-CN",
        aspectRatio: "3:4", // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
        fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
        // sources: [
        //   {
        //     type: "video/mp4", // 类型
        //     // src: 'http://clips.vorwaerts-gmbh.de/big_buck_bunny.mp4' // url地址
        //     src: "https://www.runoob.com/try/demo_source/movie.mp4", // url地址
        //   },
        // ],
        poster: "", // 封面地址
        notSupportedMessage: "此视频暂无法播放，请稍后再试", // 允许覆盖Video.js无法播放媒体源时显示的默认信息。
        // controlBar: {
        //   timeDivider: true, // 当前时间和持续时间的分隔符
        //   durationDisplay: true, // 显示持续时间
        //   remainingTimeDisplay: false, // 是否显示剩余时间功能
        //   fullscreenToggle: true, // 是否显示全屏按钮
        // },
      },
      top:'',
      barHide: false,
      nobarFirstCome: false,
      slideOut: false,
      askTosamll:false,

      isPlayed:true, //移动端用；是否已播放，视频控制条的隐藏用
      inPageTime:'',//记录页面停留时间用
      from:'', //记录跳入来源
      BpsVisible: false
    });
    document.title = "AI推广";
    // console.log("AI推广", parent, document.getElementsByClassName("cont"));
    // window.parent[0].document.getElementById("iframeAIPUB").style.position =
    //   "fixed";
    // window.parent[0].document.getElementById("iframeAIPUB").style.right = "6px";
    // window.parent[0].document.getElementById("iframeAIPUB").style.bottom =
    //   "160px";

    // 静音控制
    const volumeMuted = () => {
      const _mutedFlag = !state.muted;
      state.muted = _mutedFlag;
      document.getElementById("videoPlay" + state.ingIndex).muted = _mutedFlag;
    };

    // 预置菜单跳转播放控制
    const toIngIndex = (index, time) => {
      if(index===-1&&!state.isSmall) index=0
      if(index===-1&&state.ingIndex===-1&&!state.isSmall) state.ingIndex=0
      // document.getElementById("videoPlay").currentTime = time;
      // document.getElementById("videoPlay").play();
      // TODO 几个预置视频
      

      if (index === 3) {
       if(state.oneSreen) {
          state.formShow = false;
          state.linkBar = true;
          state.linkType = -1
       }else{
        state.formShow = true;
        state.linkBar = true;
        state.linkType = 1;
        state.step = 0;
       }
      } 
        document.getElementById("videoPlay" + state.ingIndex).pause();
        state.ingIndex = index;
        document.getElementById("videoPlay" + index).muted = index===-1?true: state.muted;
        document.getElementById("videoPlay" + index).play();
      
      if (index === 0) {
        state.linkBar = false;
        state.formShow = false;
        state.linkType = -1;
      }
      state.barHide = true
      state.nobarFirstCome = true
       setTimeout(() => {
         state.barHide = false
      }, 100);


    };

    // 联系方式选择事件
    const toLinkIndex = (index,oneSreen = state.oneSreen,from) => {
      console.log('toLinkIndex: ', from);
      state.linkType = index;
      state.formShow = true;
      state.from = from
      state.step = index===1?0:-1

      if(index===1){
        state.isFrameOneSreen = false
        state.BpsVisible = false
        document.getElementById("videoPlay" + state.ingIndex).play();
       if(!oneSreen) toIngIndex(3)
        setIframeSize()
      }

     if(oneSreen||from.includes('fromRobot')){
       document.getElementById("videoPlay" + state.ingIndex).pause();
     }
    };

    const setStep = (e) => {
      state.step = e;
    };

      const askClick = (falg) => {
        console.log('askClick: ', state.step,state.step>1);
       state.askLeave = true 
        state.askTosamll = falg
    };
    const toRobotH5 = (isSmall) => {
      // console.log('toRobotH5: ', state.isInmobile, Number(sessionStorage.getItem('IMOBILE')),Number(sessionStorage.getItem('IMOBILE'))===1);
      let _isInmobile = state.isInmobile// Number(sessionStorage.getItem('IMOBILE'))===1
      if(_isInmobile) window.open(location.origin+'/dist');
      else state.isSmall = false
    };

     const robotClick = () => {
       state.isFrameOneSreen = true
       logs(7)//location.protocol=="https:"&&logs(7)
       document.getElementById("videoPlay" + state.ingIndex).pause();
       setIframeSize()
    };

    const toPreVideo = () => {
       state.isFrameOneSreen = false
       document.getElementById("videoPlay" + state.ingIndex).play();
       setIframeSize()
    };
    
    
    

    const closeForm = (isToSmall = false,from = '') => {
      console.log('from: ', from,state.askTosamll||isToSmall);
      // isToSmall: robot页面emit的close事件
      if((state.askTosamll||isToSmall)&&!from.includes('fromRobot')) state.isSmall = true
      
      
      document.getElementById("videoPlay" + state.ingIndex).pause();
      if(isToSmall) {
        state.ingIndex = state.oneSreen?0: -1
        document.getElementById("videoPlay" + state.ingIndex).play();
      }
      
  
      state.step = -1;
      state.askLeave = false
       state.formShow = false;
       state.linkBar = false;
       if(!isToSmall){
          state.ingIndex = 0;
          document.getElementById("videoPlay0").muted = state.muted;
          document.getElementById("videoPlay0").play();
       }

       if(from.includes('fromRobot')) {
        if(from=='fromRobot'){
           state.isFrameOneSreen = false
            state.ingIndex = 0;
          document.getElementById("videoPlay0").muted = state.muted;
          document.getElementById("videoPlay0").play();
        }else{
          state.isFrameOneSreen = true
          document.getElementById("videoPlay" + state.ingIndex).pause();
           if(state.from=='fromRobot_BPS') state.BpsVisible = true
        }
        
          setIframeSize()
          
        }
       
        setTimeout(() => {
          state.from = ''
        }, 100);
      console.log((state.from&&state.from.includes('fromRobot')?true:state.isFrameOneSreen));
    };

    
    
    const closeMForm = () => {
      console.log('closeMForm: ',);
      state.formShow = false;
   
      if(state.from?.includes('fromRobot')){
        state.isFrameOneSreen = true
           state.linkType = -1
      state.linkBar = false;
      state.ingIndex = 0
      if(state.from=='fromRobot_BPS') state.BpsVisible = true
        setTimeout(() => {
          state.from = ''
        }, 100);
      }else document.getElementById("videoPlay" + state.ingIndex).play();
    }

     const preBack = () => {
      const backType = state.isFrame?state.step!==-1&&state.formShow&&(state.step>0):state.step!==-1&&!state.oneSreen&&state.formShow
      console.log('backType: ', backType,state.ingIndex);

      if(backType) askClick(false)
      else{
       if(state.ingIndex!== 0 )  {
          if( state.from?.includes('fromRobot')){
            console.log('fromRobot: ');
            state.linkType = ''
            state.formShow = false
            state.from=''
             state.isFrameOneSreen = true
             state.ingIndex = 0
          }
          else toIngIndex(0, 0)
       }

       
      }
      // backType?
      // (askClick(false)):
      // ((ingIndex !== 0 )?toIngIndex(0, 0):'')
     }
    




     const setIframeSize = () => {
        let sizeObj = { width: '140px',height: '188px'}

        if(state.isSmall){
          sizeObj =  { width: '140px',height: '188px'}
        }

        if(!state.isSmall&&!state.formShow){
          sizeObj =  { width: '272px',height: '476px'}
        }

        if(!state.isSmall&&state.formShow){
          sizeObj =  { width: '529px',height: '476px'}
        }

        if(state.isFrameOneSreen||(state.formShow&&state.from?.includes('fromRobot'))){
          sizeObj =  { width: '304px',height: '612px'}
        }
        console.log(window.location.ancestorOrigins);
        if(window.location.ancestorOrigins.length) window.top.postMessage(sizeObj, window.location.ancestorOrigins[0]);

    };

    // 事件打点记录
    const dotLog = (preVideoIndex) => {
        //展示次数
        // 每个问题点击次数,  每个视频完播率/平均播放时长 
        // 表单提交数量
      const _ctime = document.getElementById("videoPlay" + (preVideoIndex||0)).currentTime; //视频播放时长
      logs(preVideoIndex,998,_ctime)
      console.log('_ctime: ',preVideoIndex, _ctime);
       
    };

    

    // 监听视窗大小变化
    watch(
      () => state.isSmall,
      (newValue, oldValue) => {
       setIframeSize()
         dotLog()
        if(newValue){ 
            document.getElementById("videoPlay-1").muted = true;
          console.log('isSmall: ', newValue,state.ingIndex);
           state.ingIndex = -1;
          // toIngIndex(-1, 0)
            state.muted = true;
            document.getElementById("videoPlay0").pause();
            document.getElementById("videoPlay1").pause();
            document.getElementById("videoPlay2").pause();
            document.getElementById("videoPlay3").pause();
            document.getElementById("videoPlay-1").play();
          state.linkType = -1
          state.formShow = false
          state.nobarFirstCome = true
          
          logs(0)
          
         
          if(state.inPageTime) {
           logs(8,998,calcTimes(state.inPageTime)) // location.protocol=="https:"&& logs(8,998,calcTimes(state.inPageTime)) 
            console.log('PC端 停留时长calcTimes: ', 8,998,calcTimes(state.inPageTime));
          }
        }else{
          logs(-2)
          state.ingIndex = 0;
          toIngIndex(0, 0)
           if(!state.inPageTime) state.inPageTime = Date.now() //记录初入页面时间
        }

        // state.barHide = true
        // setTimeout(() => {
        //   state.nobarFirstCome = true
        // }, 1000);
      }
    );

    watch(
      () => state.formShow,
      (newValue, oldValue) => {
       setIframeSize()
       dotLog()
        if(!newValue) state.slideOut = true
        else  state.slideOut = false

      
      }
    );

    watch(
      () => state.linkType,
      (newValue, oldValue) => {
        if(newValue===1||newValue===0) logs(newValue===1?4:3) 
      }
    );

    // 监听视频切换
    watch(
      () => state.ingIndex,
      (newValue, oldValue) => {
       dotLog(oldValue)
         document.getElementById("videoPlay" + oldValue).currentTime=0;
        logs(newValue)
      }
    );



    return {
      ...toRefs(state),
      volumeMuted,
      homeVideo,
      toIngIndex,
      toLinkIndex,
      setStep,
      closeForm,
      setIframeSize,
      dotLog,
      askClick,
      miniVideoLoad,
      toRobotH5,
      robotClick,
      toPreVideo,
      closeMForm,
      preBack
    };
  },
});
