export function isEnglish(str) {
  var reg = /[a-zA-Z]/;
  return reg.test(str);
}
export function saveAudio(blob) {
  const reader = new FileReader();
  reader.readAsDataURL(blob);
  reader.onloadend = () => {
    const base64data = reader.result;
    const audioContext = new AudioContext();
    const audioBuffer = audioContext.createBufferSource();
    audioContext.decodeAudioData(
      base64ToArrayBuffer(base64data.split(",")[1]),
      (buffer) => {
        audioBuffer.buffer = buffer;
        const wavBlob = audioBufferToWavBlob(audioBuffer.buffer);
        const fileName = "audio_" + new Date().getTime() + ".wave";
        // saveAs(wavBlob, fileName);
        console.log("wavBlob: ", wavBlob, URL.createObjectURL(wavBlob));
      }
    );
  };
}

function base64ToArrayBuffer(base64) {
  const binaryString = window.atob(base64);
  const len = binaryString.length;
  const bytes = new Uint8Array(len);
  for (let i = 0; i < len; i++) {
    bytes[i] = binaryString.charCodeAt(i);
  }
  return bytes.buffer;
}

function audioBufferToWavBlob(buffers) {
  const format = 1; // PCM
  const channels = buffers.numberOfChannels;
  const sampleRate = buffers.sampleRate;
  const bitDepth = 16;
  const bytesPerSample = bitDepth / 8;
  const dataLength = buffers.length * channels * bytesPerSample;
  const bufferOffset = 44 - 8;
  const headerLength = 44;
  const fileSize = dataLength + headerLength;
  const buffer = new ArrayBuffer(fileSize);
  const view = new DataView(buffer);

  // RIFF chunk descriptor
  writeString(view, 0, "RIFF");
  view.setUint32(4, fileSize - 8, true);
  writeString(view, 8, "WAVE");

  // FMT sub-chunk
  writeString(view, bufferOffset, "fmt ");
  view.setUint32(bufferOffset + 4, 16, true);
  view.setUint16(bufferOffset + 8, format, true);
  view.setUint16(bufferOffset + 10, channels, true);
  view.setUint32(bufferOffset + 12, sampleRate, true);
  view.setUint32(
    bufferOffset + 16,
    sampleRate * channels * bytesPerSample,
    true
  );
  view.setUint16(bufferOffset + 20, channels * bytesPerSample, true);
  view.setUint16(bufferOffset + 22, bitDepth, true);

  // data sub-chunk
  writeString(view, bufferOffset + 24, "data");
  view.setUint32(bufferOffset + 28, dataLength, true);
  floatTo16BitPCM(view, bufferOffset + headerLength, buffer);

  return new Blob([view], { type: "audio/wav" });
}

function writeString(view, offset, string) {
  for (let i = 0; i < string.length; i++) {
    view.setUint8(offset + i, string.charCodeAt(i));
  }
}

function floatTo16BitPCM(output, offset, input) {
  for (let i = 0; i < input.length; i++, offset += 2) {
    const s = Math.max(-1, Math.min(1, input[i]));
    output.setInt16(offset, s < 0 ? s * 0x8000 : s * 0x7fff, true);
  }
}
