<script>
import {
  defineComponent,
  onMounted,
  reactive,
  toRefs,
  computed,
  nextTick,
  ref,
} from "vue";
import { useRouter, useRoute } from "vue-router";
import "vue3-video-play/dist/style.css";

import { Notify } from "vant";
import _ from "lodash";
import bus from "vue3-eventbus";
import store from "@/store";
import baipishu_bg from "@/assets/img/ai/baipishu_bg.png";
import preImg from "@/assets/img/ai/pre.png";

import closeImg from "@/assets/img/ai/close.png";

import { downloadFile, logs } from "@/utils/index";
import { getWhiteList, getDownloadUrl } from "@/apis/common";

export default defineComponent({
  components: {
    //videoPlay,
  },
  emits: ["closeQr", "toLinkIndex", "BpsHidevent"],
  props: {
    oneSreen: {
      type: Boolean,
    },
  },
  setup(props, ctx) {
    const iconState = reactive({
      qrImg: "https://image.ymtech.info/aistatic/20240603-180512.png",
      preImg,
      baipishu_bg,
    });

    const state = reactive({
      list: [],
    });
    const toForm = async (pdfsrc, fileName = "", fileSuffix) => {
      logs(10); // location.protocol == "https:" && logs(10);

      let { code, data, msg } = await getDownloadUrl({
        urlAddress: pdfsrc,
        fileName,
        fileSuffix,
      });
      if ((code = 200)) {
        store.commit("mSetDownInfo", {
          pdfsrc: data,
          fileName,
          oneSreen: props.oneSreen,
        });
        // downloadFile(
        //   store.state.downInfo.pdfsrc,
        //   store.state.downInfo.fileName
        // );
        // window.open(store.state.downInfo.pdfsrc, "_blank");
        ctx.emit("toLinkIndex", 1, "fromRobot_BPS");
      } else
        Notify({
          message: msg,
          className: props.oneSreen ? "oneSreen" : "",
        });
    };

    const getData = async () => {
      let { code, data, msg } = await getWhiteList();
      console.log("code, data, msg: ", code, data, msg);
      if (code == 200) state.list = data;
      else
        Notify({
          message: msg,
          className: props.oneSreen ? "oneSreen" : "",
        });
    };
    getData();

    const toPage = () => {
      console.log(store.state.downInfo.pdfsrc);
      // if (store.state.downInfo?.pdfsrc && store.state.downInfo?.pdfsrc?.length)
      ctx.emit("closeQr", "baipishu");
      // else ctx.emit("BpsHidevent");
    };

    const toMore = () => {
      logs(12); //location.protocol == "https:" && logs(12);
      window.open("https://www.eclicktech.com.cn/html/report");
    };

    return {
      ...toRefs(state),
      ...toRefs(iconState),
      toPage,
      toForm,
      toMore,
      getData,
    };
  },
});
</script>
