<template>
  <!-- :class="isMobile ? 'isMobile' : 'isPC' oneSreen" -->
  <div
    class="formLink"
    :class="{ oneSreen }"
    :style="focusKey && oneSreen ? 'overflow-y: auto;' : ''"
  >
    <!-- <div class="tool-bar" v-if="oneSreen">
      <van-image
        :src="preImg"
        class="pre-icon icon"
        @click="closeForm"
      ></van-image>
    </div> -->
    <p
      v-if="step < 5"
      class="process"
      :style="
        'width:calc( ' + 100 / (5 - step) + '%' + (oneSreen ? '' : ' - 12px )')
      "
    ></p>

    <van-form>
      <div class="slide-left bodycont" v-if="step === 0">
        <p class="title">怎么称呼和联系您呢？</p>
        <van-field
          style="margin-top: 4px"
          v-model="form.username"
          name="username"
          label="姓名"
          placeholder="姓名"
          @focus="focusEvent('username')"
          @blur="focusKey = ''"
          :class="{ isfocus: focusKey === 'username' }"
        />
        <van-field
          v-model="form.company"
          name="company"
          label="公司"
          placeholder="公司"
          @focus="focusEvent('company')"
          @blur="focusKey = ''"
          :class="{ isfocus: focusKey === 'company' }"
        />
        <van-field
          v-model="form.positionName"
          name="position"
          label="职位"
          placeholder="职位"
          right-icon="play"
          @focus="focusEvent('position')"
          @click="pullPop('YW_list', '请选择职位', 'position')"
          :class="{ isfocus: focusKey === 'position' }"
          readonly
        />

        <van-divider :style="{ borderColor: '#D6DEFF' }" />

        <van-field
          v-model="form.phone"
          name="phone"
          label="手机"
          placeholder="手机"
          @focus="focusEvent('phone')"
          @blur="focusKey = ''"
          :class="{ isfocus: focusKey === 'phone' }"
          class="verfyCode"
        >
          <template #right-icon>
            <van-button
              v-if="form.phone"
              type="primary"
              class="btn-ver"
              size="mini"
              @click="canGetVer && getCode()"
              ><van-count-down
                ref="countDown"
                :time="curTime"
                format="ss秒"
                :autoStart="false"
                :class="{ hide: canGetVer }"
              >
              </van-count-down
              >{{ canGetVer ? "获取验证码" : "" }}</van-button
            >
          </template>
        </van-field>

        <!-- <van-field
          v-model="form.phone"
          name="phone"
          label="手机"
          placeholder=""
          @focus="focusEvent('phone')"
          class="noSPLable"
          :class="{ isfocus: focusKey === 'phone' }"
        >
          <template #label>
            {{ linkType === 1 ? "手机" : "邮箱" }}
            <van-icon
              name="play"
              style="
                transform: rotate(90deg);
                margin-left: 6pt;
                cursor: pointer;
              "
              @click="pullPop('link_list', '请选择联系方式', 'phone')"
            />
          </template>
        </van-field> -->

        <van-field
          v-model="form.verfyCode"
          name="verfyCode"
          label="验证码"
          placeholder="验证码"
          @focus="focusEvent('verfyCode')"
          @blur="focusKey = ''"
          :class="{ isfocus: focusKey === 'verfyCode' }"
        >
        </van-field>
        <van-field
          v-model="form.email"
          name="email"
          label="邮箱"
          placeholder="邮箱"
          @focus="focusEvent('email')"
          @blur="emailBlur"
          :class="{ isfocus: focusKey === 'email' }"
          style="margin-top: 8px"
        />
      </div>
      <div class="slide-left" v-if="step === 1 || step === 2">
        <p class="title">
          {{
            { 1: "您业务所处的行业：", 2: "您希望通过我们获得哪些服务：" }[step]
          }}
        </p>
        <div class="content-choose" :class="{ mini: step === 2 }">
          <p
            class="i-choose"
            :class="{
              active:
                step === 1
                  ? form['hyCode'] === item.code
                  : form['fwCode'].includes(item.code),
              mini: step === 2,
            }"
            v-for="(item, index) in { 1: HY_list, 2: FW_list }[step]"
            @click="
              selectPicker(item.code, { 1: 'hyCode', 2: 'fwCode' }[step], step)
            "
          >
            {{ item.zhCn }}
          </p>
        </div>
      </div>
      <div class="slide-left" v-if="step === 3">
        <p class="title">您本次期待合作的媒体：</p>
        <div class="content-choose" :class="{ mini: step === 3 }">
          <p
            class="i-choose"
            :class="{
              active: form['comedia'].includes(item.code),
              mini: step === 3,
            }"
            v-for="(item, index) in co_list"
            @click="selectPicker(item.code, 'comedia', step)"
          >
            {{ item.zhCn }}
          </p>
        </div>
      </div>

      <div class="slide-left" v-if="step === 4">
        <p class="title">一些补充信息：</p>

        <van-field
          v-model="form.locationName"
          name="location"
          label=""
          placeholder="公司所在区域"
          right-icon="play"
          @blur="focusKey = ''"
          @focus="focusEvent('location')"
          @click="pullPop('GJ_list', '公司所在区域', 'location')"
          :class="{ isfocus: focusKey === 'location' }"
          readonly
        />
        <van-field
          v-if="form.locationName == '中国'"
          v-model="form.province"
          name="province"
          label=""
          placeholder="公司所在省份"
          right-icon="play"
          @blur="focusKey = ''"
          @focus="focusEvent('province')"
          @click="pullPop('proList', '公司所在省份', 'province')"
          :class="{ isfocus: focusKey === 'province' }"
          readonly
        />
        <!-- <van-field
          v-model="form.stageName"
          name="stage"
          label=""
          placeholder="业务发展阶段"
          right-icon="play"
          @blur="focusKey = ''"
          @focus="focusEvent('stage')"
          @click="pullPop('JD_list', '业务发展阶段', 'stage')"
          :class="{ isfocus: focusKey === 'stage' }"
          readonly
        /> -->
        <van-field
          v-model="form.quarterlyPromotionBudgetName"
          name="quarterlyPromotionBudget"
          label=""
          placeholder="季度推广预算"
          right-icon="play"
          @blur="focusKey = ''"
          @focus="focusEvent('quarterlyPromotionBudget')"
          @click="
            pullPop('YS_list', '季度推广预算', 'quarterlyPromotionBudget')
          "
          :class="{ isfocus: focusKey === 'quarterlyPromotionBudget' }"
          readonly
        />
      </div>
      <div class="slide-left contact" v-if="step === 5">
        <van-image :src="contactImg" class="icon"></van-image>
        <p class="title">我们的同事会尽快与您取得联系！</p>

        <div class="closeBtn" @click="closeForm()">关闭</div>
      </div>

      <div
        v-if="step === 4"
        class="slide-left btnNextBorder fixed"
        :class="{ isActive: getCanNext(step) }"
        @click="submit(form)"
      >
        <div class="btnNext">提交</div>
      </div>
      <div
        v-else
        class="slide-left btnNextBorder"
        :class="{
          isActive: getCanNext(step),
          fixed: step > 0,
          fixed: step > 0,
          nofixed: step == 0,
        }"
        @click="getCanNext(step) ? step < 4 && nextOp(step + 1) : ''"
      >
        <!-- :style="
          step == 0 ? 'margin-top: calc(100vh - ' + bodycontH + 'pt)' : ''
        " -->
        <div
          v-if="step < 5"
          class="btnNext"
          @click="step === 4 ? submit(form) : ''"
        >
          {{ step < 4 ? "下一步" : "提交" }}
        </div>
      </div>
    </van-form>

    <!-- 弹窗 -->
    <van-popup
      v-model:show="popFlag"
      round
      position="bottom"
      :style="{ height: '100%' }"
    >
      <div
        class="title-picker flex align-center justify-between"
        :class="{ oneSreen }"
      >
        <span
          class="van-picker__cancel van-haptics-feedback"
          @click="onSelectCancel()"
        >
          取消
        </span>

        <div class="van-picker__title van-ellipsis">{{ popTitle }}</div>

        <span
          class="van-picker__confirm van-haptics-feedback"
          @click="onSelectConfirm()"
        >
          确定
        </span>
      </div>
      <!-- <van-picker
        :title="popTitle"
        :columns="popMap"
        @confirm="onSelectConfirm"
        @cancel="onSelectCancel"
        :columns-field-names="customFieldName"
      /> -->
      <div class="picker">
        <div
          class="i-picker flex align-center justify-between"
          :class="{
            active:
              selectItem.code ===
              (focusKey === 'province' || focusKey === 'location'
                ? item
                : item.code),
          }"
          v-for="item in popMap"
          :key="
            focusKey === 'province' || focusKey === 'location'
              ? item
              : item.code
          "
          @click="
            onPicker(
              focusKey === 'province' || focusKey === 'location'
                ? item
                : item.code,
              focusKey === 'province' || focusKey === 'location'
                ? item
                : item.zhCn
            )
          "
        >
          {{
            focusKey === "province" || focusKey === "location"
              ? item
              : item.zhCn
          }}
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
import {
  defineComponent,
  onMounted,
  reactive,
  toRefs,
  computed,
  ref,
} from "vue";
import { useRouter, useRoute } from "vue-router";
import "vue3-video-play/dist/style.css";
import { getDictList, getVCode, submitHttp } from "@/apis/common";
import { Notify } from "vant";
import _ from "lodash";
import bus from "vue3-eventbus";
import store from "@/store";
import contactImg from "@/assets/img/ai/co.png";
import preImg from "@/assets/img/ai/pre.png";
import {
  proList,
  countryList,
  PHONE_REG1,
  EMAIL_REG,
  logs,
  downloadFile,
} from "@/utils/index";

export default defineComponent({
  components: {
    //videoPlay,
  },
  emits: ["setStep", "closeForm"],
  props: {
    step: {
      type: Number,
    },
    oneSreen: {
      type: Boolean,
    },
    from: {
      type: String,
    },
  },
  setup(props, ctx) {
    const countDown = ref(null);
    onMounted(() => {
      // 字典获取
      getDictData();

      const bodycont = document.getElementsByClassName("bodycont")[0];
      console.log("bodycont: ", bodycont, bodycont?.offsetHeight);
      state.bodycontH = bodycont?.offsetHeight + 60;
    });
    const state = reactive({
      thisStep: computed(() => {
        return props.step;
      }), //form填写的第几步，共四步
      YW_list: [], // 业务类型
      GJ_list: countryList, // 国家
      HY_list: [], // 客户行业
      FW_list: [], // 服务类型
      JD_list: [], // 业务阶段
      YS_list: [], // 推广预算
      co_list: [
        { code: "XSHZMT-1", zhCn: "Google/Youtube" },
        { code: "XSHZMT-2", zhCn: "TikTok" },
        { code: "XSHZMT-3", zhCn: "Meta/Ins" },
        { code: "XSHZMT-4", zhCn: "ASA/bing" },
        { code: "XSHZMT-5", zhCn: "苹果广告" },
        { code: "XSHZMT-6", zhCn: "巨量引擎" },
        { code: "XSHZMT-7", zhCn: "快手" },
        { code: "XSHZMT-8", zhCn: "腾讯" },
        { code: "XSHZMT-9", zhCn: "爱奇艺" },
        { code: "XSHZMT-10", zhCn: "程序化营销" },
        { code: "XSHZMT-11", zhCn: "网红营销" },
        { code: "XSHZMT-12", zhCn: "本地化媒体营销" },
        { code: "XSHZMT-13", zhCn: "CPS营销" },
      ],
      link_list: [
        { zhCn: "手机", code: 1 },
        { zhCn: "邮箱", code: 2 },
      ],
      linkType: 1,
      focusKey: "",
      popMap: [],
      popTitle: "",
      popFlag: false,
      customFieldName: {
        text: "zhCn",
        value: "code",
      },
      form: {
        username: "",
        company: "",
        position: "",
        positionName: "",
        phone: "",
        email: "",
        verfyCode: "",
        hyCode: "",
        fwCode: [],
        location: "",
        locationName: "",
        province: "",
        stage: "",
        stageName: "",
        quarterlyPromotionBudget: "",
        quarterlyPromotionBudgetName: "",
        comedia: [],
      },
      selectItem: {
        code: "",
        zhCn: "",
      },
      contactImg,
      proList,
      curTime: 60000,
      canGetVer: true,
      preImg,
      isDownFiled: false,
      bodycontH: 0,
    });

    // 字典数据
    const getDictData = async () => {
      // 业务类型
      // let { data: data } = await getDictList({
      //   typeCode: "YWLXRZW",
      //   status: 1,
      // });
      // 国家
      // let { data: data1 } = await getDictList({
      //   typeCode: "GJDQ",
      //   status: 1,
      // });
      // 客户行业
      // let { data: data2 } = await getDictList({
      //   typeCode: "XSHY",
      //   status: 1,
      // });
      // 服务类型
      // let { data: data3 } = await getDictList({
      //   typeCode: "FWNR",
      //   status: 1,
      // });
      // 业务阶段
      // let { data: data4 } = await getDictList({
      //   typeCode: "GSYWJD",
      //   status: 1,
      // });
      // 推广预算
      // let { data: data5 } = await getDictList({
      //   typeCode: "JDTGYS",
      //   status: 1,
      // });
      state.YW_list = [
        { code: "YWLXRZW-1", zhCn: "董事长/CEO/VP/首席官" },
        { code: "YWLXRZW-2", zhCn: "高管/项目负责人/总监" },
        { code: "YWLXRZW-3", zhCn: "中层管理/经理/主管" },
        { code: "YWLXRZW-4", zhCn: "基层管理/BD/业务联系人" },
        { code: "YWLXRZW-5", zhCn: "其他" },
      ]; //data; 职位
      // state.GJ_list = countryList; //data1; 国家

      state.HY_list = [
        { code: "XSHY-1", zhCn: "电商" },
        { code: "XSHY-2", zhCn: "游戏" },
        { code: "XSHY-3", zhCn: "移动应用" },
        { code: "XSHY-4", zhCn: "教育" },

        { code: "XSHY-8", zhCn: "品牌" },
        { code: "XSHY-9", zhCn: "泛娱乐" },
        { code: "XSHY-10", zhCn: "金融科技" },
      ]; //data2;
      state.FW_list = [
        { code: "FWNR-1", zhCn: "新用户增长" },
        { code: "FWNR-2", zhCn: "媒体开户" },
        { code: "FWNR-3", zhCn: "市场调研" },
        { code: "FWNR-4", zhCn: "创意设计" },
        { code: "FWNR-5", zhCn: "社媒运营" },
        { code: "FWNR-6", zhCn: "推广投放" },
        { code: "FWNR-7", zhCn: "独立站建站" },
        { code: "FWNR-8", zhCn: "品牌本地化" },
        { code: "FWNR-9", zhCn: "直播电商" },
        { code: "FWNR-10", zhCn: "产品试用" },
      ]; //data3; 服务
      // state.JD_list = //data4; 业务发展阶段
      state.YS_list = [
        { code: "JDTGYS-1", zhCn: "30万美金以下" },
        { code: "JDTGYS-2", zhCn: "30万-60万美金" },
        { code: "JDTGYS-3", zhCn: "60万美金以上" },
        { code: "JDTGYS-4", zhCn: "暂无预算" },
      ]; //data5;季度推广预算
    };

    // 联系方式选择事件
    const focusEvent = (key) => {
      state.focusKey = key;
    };
    // 唤起选择事件
    const pullPop = (mapkey, title, focusKey) => {
      state.focusKey = focusKey;
      state.popMap = state[mapkey];
      console.log("state[mapkey]: ", mapkey, state[mapkey]);
      state.popTitle = title;
      state.popFlag = true;
    };
    const onSelectConfirm = () => {
      if (state.focusKey == "phone") {
        state.form["phone"] = "";
        state.linkType = state.selectItem.code;
      } else {
        state.form[state.focusKey] = state.selectItem.code;
        state.form[state.focusKey + "Name"] = state.selectItem.zhCn;
        state.focusKey = "";
      }

      state.popFlag = false;
    };
    const onSelectCancel = () => {
      state.focusKey = "";
      state.popMap = [];
      state.popTitle = "";
      state.popFlag = false;
    };

    const onPicker = (code, zhCn) => {
      console.log("onPicker: ", code, zhCn);
      if (state.focusKey === "location" && zhCn !== "中国")
        state.form.province = "";
      state.selectItem = {
        code,
        zhCn,
      };
      onSelectConfirm();
    };

    const emailBlur = () => {
      state.form.email = state.form.email.trim();
      if (!state.form.email) return;
      if (!EMAIL_REG.test(state.form.email)) {
        Notify({
          message: "请输入正确的邮箱地址！",
          className: props.oneSreen || props.from ? "oneSreen" : "",
        });
      } else state.focusKey = "";
    };

    // 验证码接口结果回调
    const httpVerCallback = (res) => {
      console.log("httpVerCallback: ", res);
      if (res.msg == "OK") {
        Notify({
          type: "success",
          message: "验证码已发送，请注意查收！",
          className: props.oneSreen || props.from ? "oneSreen" : "",
        });
        // 手机号
        state.canGetVer = false;
        countDown.value.reset();
        countDown.value.start();

        setTimeout(() => {
          state.canGetVer = true;
        }, state.curTime);
      } else
        Notify({
          message: res.msg,
          className: props.oneSreen || props.from ? "oneSreen" : "",
        });
    };

    const getCode = async (e) => {
      console.log("getCode: ", countDown);
      state.form.phone = state.form.phone.trim();
      if (!state.form.phone) return;
      if (!PHONE_REG1.test(state.form.phone)) {
        Notify({
          message: "请输入正确的手机号码！",
          className: props.oneSreen || props.from ? "oneSreen" : "",
        });
      } else {
        const xhr = new XMLHttpRequest();
        const url =
          window.origin +
          "/index.php?s=custom&c=sms&m=code&callback=httpVerCallback";

        xhr.open("POST", url, true);
        xhr.setRequestHeader(
          "Content-Type",
          "application/x-www-form-urlencoded"
        );

        xhr.onreadystatechange = function () {
          if (xhr.readyState === 4) {
            if (xhr.status === 200) {
              try {
                console.log("xhr", xhr.responseText);
                eval(xhr.responseText);
              } catch (e) {
                console.error("解析响应时发生错误", e);
              }
            } else {
              console.error("网络请求异常");
            }
          }
        };

        const params = `phone=${encodeURIComponent(state.form.phone)}`;
        xhr.send(params);
      }
    };

    // 表单提交接口结果回调
    const httpFormCallback = (res) => {
      console.log("httpVerCallback: ", res);
      if (res.msg == "OK" || res.msg.indexOf("成功") > -1) {
        nextOp(state.thisStep + 1);

        // 处理从白皮书进来的下载
        if (store.state.downInfo?.pdfsrc) {
          logs(11); // location.protocol == "https:" && logs(11);

          sessionStorage.setItem("isDownFiled", 1);
          // 下载的参数
          if (store.state.downInfo.oneSreen) {
            // 手机端
            window.open(store.state.downInfo.pdfsrc, "_blank");
          } else {
            downloadFile(
              store.state.downInfo.pdfsrc,
              store.state.downInfo.fileName
            );
          }
        }
      } else
        Notify({
          message: res.msg,
          className: props.oneSreen || props.from ? "oneSreen" : "",
        });
    };

    const submit = async (form) => {
      let fwCode = JSON.parse(JSON.stringify(form.fwCode));
      console.log("form: ", form, fwCode);
      // ctx.emit("submitForm", true);
      // https://www.eclicktech.com.cn/index.php?s=form&c=crmlxwm&m=post
      let formData = {
        is_form: 1,
        is_admin: 0,
        is_tips: "",
        csrf_test_name: "0444dc3acee45144b9d2cccf9b4079c3",
        "data[linkManName]": form.username,
        "data[linkManEmail]": form.email,
        "data[linkManPhone]": form.phone,
        "data[smsCode]": form.verfyCode,
        "data[linkManWeChat]": "",
        "data[cusName]": form.company,
        "data[position]": form.position,
        "data[location]": form.location,
        "data[province]": form.province,
        "data[trade]": form["hyCode"],
        "data[serviceContent]": fwCode.join(),
        "data[coopMedia]": form["comedia"].join(),
        "data[quarterlyRromotion_budget]": form.quarterlyPromotionBudget,
        "data[remark]": "",
      };

      console.log("formData: ", formData);
      const xhr = new XMLHttpRequest();

      const url =
        window.origin +
        "/index.php?s=form&c=crmlxwm&m=post&callback=httpFormCallback";

      xhr.open("POST", url, true);
      xhr.setRequestHeader("Content-Type", "application/x-www-form-urlencoded");
      logs(5);
      xhr.onreadystatechange = function () {
        if (xhr.readyState === 4) {
          if (xhr.status === 200) {
            try {
              console.log(xhr.responseText, xhr);
              httpFormCallback(JSON.parse(xhr.responseText));
              // eval(xhr.responseText);
            } catch (e) {
              console.error("解析响应时发生错误", e);
            }
          } else {
            console.error("网络请求异常");
            nextOp(state.thisStep + 1);
            // 处理从白皮书进来的下载
            if (store.state.downInfo?.pdfsrc) {
              // 下载的参数
              sessionStorage.setItem("isDownFiled", 1);
              logs(11);
              if (store.state.downInfo.oneSreen) {
                // 手机端
                window.open(store.state.downInfo.pdfsrc, "_blank");
              } else {
                downloadFile(
                  store.state.downInfo.pdfsrc,
                  store.state.downInfo.fileName
                );
              }
            }
          }
        }
      };
      // 测试数据
      //   {
      //   csrf_test_name: "0444dc3acee45144b9d2cccf9b4079c3",
      //   "data[coopMedia]": "",
      //   "data[cusName]": "wwcc",
      //   "data[linkManEmail]": "",
      //   "data[linkManName]": "qian.ning",
      //   "data[linkManPhone]": "15706083686",
      //   "data[linkManWeChat]": "",
      //   "data[location]": "土库曼斯坦",
      //   "data[position]": "YWLXRZW-1",
      //   "data[province]": "",
      //   "data[quarterlyRromotion_budget]": "JDTGYS-1",
      //   "data[remark]": "",
      //   "data[serviceContent]": "FWNR-10,FWNR-8",
      //   "data[smsCode]": "111",
      //   "data[trade]": "XSHY-1",
      //   is_admin: 0,
      //   is_form: 1,
      //   is_tips: "",
      // }
      xhr.send(new URLSearchParams(formData).toString());
    };

    const getCanNext = (step) => {
      return {
        0:
          state.form.username !== "" &&
          state.form.company !== "" &&
          state.form.position &&
          state.form.phone &&
          state.form.email &&
          EMAIL_REG.test(state.form.email) &&
          state.form.verfyCode,
        1: state.form["hyCode"],
        2: state.form["fwCode"].length,
        3: state.form["comedia"].length,
        4:
          state.form.location &&
          (state.form.locationName == "中国" ? state.form.province : true) &&
          state.form.quarterlyPromotionBudget,
      }[step];
    };

    const closeForm = () => {
      let _isDownFiled = Number(sessionStorage.getItem("isDownFiled")) === 1;
      console.log(
        "state.isDownFiled ? false : true: ",
        _isDownFiled,
        props.from
      );
      ctx.emit(
        "closeForm",
        _isDownFiled ? false : props.from ? false : false,
        _isDownFiled ? "fromRobot_BPS" : props.from
      );
      setTimeout(() => {
        sessionStorage.setItem("isDownFiled", 0);
      }, 3000);
    };

    const nextOp = (step) => {
      ctx.emit("setStep", step);
      console.log("step: ", step);
    };

    const selectPicker = (code, key, step) => {
      if (step === 1) state.form[key] = code;
      else {
        let setFwCode = new Set(state.form[key]);
        if (setFwCode.has(code)) setFwCode.delete(code);
        else setFwCode.add(code);

        state.form[key] = Array.from(setFwCode);
      }
      // setTimeout(() => {
      //   ctx.emit("setStep", step + 1);
      // }, 2000);
    };

    return {
      ...toRefs(state),
      focusEvent,
      pullPop,
      onSelectConfirm,
      onSelectCancel,
      onPicker,
      selectPicker,
      closeForm,
      nextOp,
      getCanNext,
      submit,
      getCode,
      countDown,
      httpFormCallback,
      httpVerCallback,
      emailBlur,
    };
  },
});
</script>
