// 封装请求
import rule from "./rule";
import axios from "axios";
import qs from "qs";
import router from "../router";
import store from "../store";
import bus from "vue3-eventbus";
import { Notify } from "vant";
// import store from '../store'
import { Toast } from "vant";
import localStorageUtil from "@/utils/localStorageUtil";
import cache from "./cache";
import { useRouter, useRoute } from "vue-router";
const route = useRoute();

const baseUrlMap = new Map([
  ["localhost", "http://creative-aigc-gateway.aigc-test.svc.cluster.local/api"],
  [
    "172.30.90.190",
    "http://creative-aigc-gateway.aigc-test.svc.cluster.local/api",
  ],
  // ["172.30.90.190", "http://172.30.91.141:9940/api"],
]);

//如果有上面的几个走上面几个，没有的话走默认，可扩展性强一些
const rootbBaseUrl = baseUrlMap.get(location.hostname)
  ? baseUrlMap.get(location.hostname)
  : "https://api.kreadoai.com/api";

const _ISPROD = location.protocol == "https:";

// 全局设置
axios.defaults.withCredentials = true;
const errHandler = () => {
  Toast.fail(lang[localStorage.getItem("lang") || "zh"].error);
};
// 服务器错误信息国际化
const lang = {
  en: {
    error: "The server responded incorrectly, please try again later",
    acRepeat: "This binding relationship already exists",
    unbind: "Unbind failed! Until you unbind the oﬀer and try again!",
  },
  zh: {
    error: "服务器未响应，请稍后重试",
    acRepeat: "此绑定关系已存在",
    unbind: "解绑失败！请先解除与oﬀer绑定关系再试！",
  },
  ja: {
    error: "サーバーが応答していません。しばらくしてからもう一度お試しください",
    acRepeat: "この結合関係は既に存在します",
    unbind:
      "失敗するまで！ まず案件との関係を解き、もう一度やり直してください。",
  },
};

const CancelToken = axios.CancelToken;
const source = CancelToken.source();
export default class BaseRequest {
  constructor(baseURL) {
    this.http = axios.create({
      baseURL: rootbBaseUrl + baseURL,
      adapter: cache({
        time: 1000,
      }),
    });

    // 全局默认配置
    // this.http.defaults.headers["Authorization"] =
    //   localStorage.getItem("ADMIN-TOKEN") || "";
    // 请求拦截器
    this.http.interceptors.request.use(
      (config) => {
        config.headers["openingId"] = _ISPROD
          ? "1834187106135429121"
          : "1834418827733667841";
        config.headers["taskId"] = _ISPROD
          ? "1834188589187063809"
          : "1834436629329764353";
        config.headers["sessionId"] = sessionStorage.getItem("sessionId");
        config.headers["Language"] = "zh";

        config.cancelToken = source.token;
        return config;
      },
      (error) => {
        return Promise.reject(error);
      }
    );
    // 响应拦截器
    this.http.interceptors.response.use(
      (response) => {
        //这里为啥不直接写成response.data.data，是留个口子，万一后面有扩展
        const data = response.data || "";
        const msg = response.data.msg || "";
        switch (response.status) {
          case rule.ok:
            if (data.code !== 200 && typeof data !== "string") {
              console.log(route);
              Notify({
                message: data.msg,
                className: !window.location.ancestorOrigins.length
                  ? "oneSreen"
                  : "",
              });
            }
            return Promise.resolve(data);
            break;
          case rule.ercode:
            Toast.fail(msg);
            return Promise.resolve({
              type: "vcode",
              result: response.data,
            });

          default:
            Toast.fail(msg);
            return Promise.resolve(data);
        }
      },
      (error) => {
        errHandler();
        return Promise.reject(error);
      }
    );
  }
  request({ url, method, params, type }) {
    if (method === "get") {
      return this.get(url, params);
    } else {
      if (type === "query") {
        return this.postForm(url, params);
      } else {
        return this.post(url, params);
      }
    }
  }
  post(url, params = {}, config = {}) {
    return this.http.post(url, params, config);
  }
  postForm(url, params = {}, config = {}) {
    console.log("params: ", params, qs.stringify(params));
    const formData = new FormData();
    formData.append("multipartFile", params.multipartFile, "audio.wav"); // 将 WAV 文件添加到 FormData

    formData.append("conversationId", params.conversationId);
    console.log("formData: ", formData, qs.stringify(formData));
    return this.http({
      url,
      method: "post",
      headers: {
        "Content-Type": "application/x-www-form-urlencoded",
      },
      ...config,
      data: qs.stringify(params),
    });
  }
  get(url, params = {}, config = {}) {
    let _config = {
      ...config,
      headers: {
        "Content-Type": "application/x-www-form-urlencoded",
      },
      params,
      paramsSerializer: function (params) {
        return qs.stringify(params, {
          arrayFormat: "repeat",
          encode: true,
        });
      },
      data: {},
    };
    _config = Object.assign(_config, config) || {};
    return this.http.get(url, _config);
  }
  postMul(url, params, config) {
    const formData = new FormData();
    Object.keys(params).forEach((key) => {
      // 为null时 formData会自动转为字符串'null'，所以中间做这层处理
      if (key == "multipartFile") {
        // 生成文件名
        const filename = "audio.wav";
        console.log("filename: ", filename);
        formData.append("multipartFile", params["multipartFile"], filename);
      } else {
        formData.append(key, params[key] === null ? "" : params[key]);
      }
    });
    // console.log("formData: ", formData);
    return this.http({
      url,
      method: "post",
      headers: {
        "Content-Type": "multipart/form-data",
      },
      data: formData,
      ...config,
      onDownloadProgress: (progressEvent) => {
        console.log(
          "stopThink: ",
          Boolean(sessionStorage.getItem("stopThink") == "true")
        );
        const data = onAudioToTextProgress(
          { text: "", loading: true, speechToTextTaskId: null, error: false },
          progressEvent.target.response
        );
        console.log("progressEvent: ", data);

        if (Boolean(sessionStorage.getItem("stopThink")) == "true") return;
        bus.emit("STREAM-RESULT", data);
      },
    });
  }
}

const onAudioToTextProgress = (obj, responseText) => {
  console.log("onAudioToTextProgress responseText", responseText);

  if (/\[speechToTextTaskId-[\d]{19}-DONE\]$/.test(responseText)) {
    console.log("speechToTextTaskId match end");
    let index = responseText.lastIndexOf("[speechToTextTaskId-");
    obj.text = responseText?.substring(0, responseText.length - 45);
    obj.loading = false;
    obj.speechToTextTaskId = responseText?.substring(
      index + 20,
      responseText.length - 6
    );
  } else if (/\[code-65001-(.*?)-DONE\]$/.test(responseText)) {
    // 如果参数错误（参数为null、语音文件超过规定时长、不是wav格式音频、上传失败）
    const index = responseText.lastIndexOf("[code-65001-");
    const msg = responseText?.substring(index + 12, responseText?.length - 6);
    obj.text = "参数错误";
    obj.loading = false;
    obj.error = true;
  } else if (/\[code-55055-(.*?)-DONE\]$/.test(responseText)) {
    // 会话过期
    obj.loading = false;
    obj.text = "会话过期";
  } else {
    obj.text = responseText;
    obj.loading = true;
  }
  return obj;
};
